* {
  box-sizing: border-box;
}
body{
  margin: 0px;
}
.header-container {
   position: relative;
   top: 0;
   z-index: 10;
   background: white;
   height: 80px;
   padding: 10px;
   border-bottom: 1px solid lightgrey;
   min-width: 500px;
}
.header-container > .logo {
  margin-left: 50px;
  float: left;
}
.header-container > .logo > img {
  width: 60px;
  height: 60px;
  border-radius: 5px; 
  float: left;
}
.header-container > .logo > .logo-txt {
  float: left;
  margin-left: 10px;
  margin-top: 25px;
  font-style: italic;
  font-size: 14px;
  color: gray;
  font-weight: bold;
}
.logo-txt > div:last-child{
  margin-left: 10px;
  font-size: 12px;
}
.right-menu {
  float: right;
  margin-right: 10px;
  margin-top: 25px;
  font-size: 14px;
  font-weight: bold;  
  color: gray;
}
.logout{  
  float: right;
  cursor: pointer;
  margin-right: 10px;
  font-family: monospace;
}
.logout > div:hover {  
  text-decoration: underline;
}
.user{
  float: right;
  margin-right: 30px;
  font-family: monospace;
  color: gray;
}
.sign-section {
  background-image: url('https://images.unsplash.com/photo-1548092372-0d1bd40894a3?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80');
  background-size: cover;
}
.sign-container {
    margin: 0 auto;
    max-width: 500px;
    min-height: calc(100vh - 80px);
    font-family: monospace;
}
.sign-container:before {
  content: '';
  height: 80px;
  display: block;
}
.sign-container > .sign-sub-container {
  padding: 40px;
  border-radius: 5px;
  min-height: 400px;
  background-color: rgba(0,0,0,.75);
  color: white;
}
.sign-container >
.sign-sub-container > 
.sign-fields-container , .sign-actions-container {
  width: 100%;
  min-width: 300px;
}
.sign-container >
.sign-sub-container > 
.sign-fields-container >
.input-field{
    position: relative;
    left: 0;
    margin-bottom: 30px;    
}
.sign-container >
.sign-sub-container > 
.sign-fields-container >
.input-field:first-child {
  margin-top: 30px;
}
.sign-container >
.sign-sub-container > 
.sign-fields-container >
.input-field > 
label{
    position: absolute;
    left: 10px;
    top: 2px;
    color: gray;
    font-size: 12px;
}
.sign-container >
.sign-sub-container > 
.sign-fields-container >
.input-field > 
input
{
    width: 100%;
    height: 45px;
    padding: 10px 10px;
    border-radius: 3px;
    border: 0px;    
    font-size: 16px;
    outline: 0px;
    cursor: pointer;
}
.sign-container >
.sign-sub-container > 
.sign-actions-container >
button{
    width: 100%;
    margin-top: 20px;
    margin-bottom: 10px;
    height: 45px;
    border-radius: 3px;
    border: 0px; 
    font-size: 16px;
    font-weight: bold;
    background: gray;
    color: white;
    cursor: pointer;
}
.error-text{
  color: red;
  width: 100%;
  white-space: normal;
  word-break: break-word;
}

.home-section {
  max-width: 1000px;
  min-width: 500px;
  margin: 0 auto;
}
.home-section:before{
  content: '';
  height: 50px;
  display: block;
}
.grid-container {
  position: relative;
  height: 400px;
  margin: 40px auto;
  overflow: hidden;
  box-shadow: 1px 1px 14px #e4e4e4;
  width: 100%;
  float: left;
}
.search-container > input{
  width: 30%;
  max-width: 400px;
  min-width: 200px;
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 5px;
}
.search-container > button {
  width: 30%;
  margin: 0 auto;
  display: block;
  margin-bottom: 20px;
  padding: 5px;
  cursor: pointer;
}
.home-section > .home-header{
  text-align: center;
}
.grid-header-container, .grid-body-container{
  position: relative;
}
.header-row-container, .body-row-container{
  position: absolute;
  border-bottom: 1px solid #d2d2d2;
}
.header-cell, .body-cell{
  position: absolute;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 15px;
  font-family: monospace;;
}
.header-cell {
  font-weight: bold;
}
.header-row-container {
  border-bottom: 1px solid gray;
}
.grid-header-view {
  overflow: hidden;
}
.grid-body-view {
  overflow: auto;
}
.footer {
  position: fixed;
  bottom: 10px;
  left: 10px;
}
.pageSearch {
 background-color: gray;
 width: 500px;
 padding: 10px;
 border-radius: 5px;
 position: relative;
}
.pageSearch:before{
  content:'';
  display:block;
  width:0;
  height:0;
  position:absolute;
  
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent; 
  border-right:8px solid gray;
  left:-8px;
  
  top:23px;
}
.pageSearch > input {
  width: 100%;
  padding: 10px;
  padding-right: 130px;
  font-size: 16px;
  font-family: monospace;
}
.pageSearch > .action-text {
  position: absolute;
  right: 20px;
  top: 23px;
}
.action-text > span {
  margin-right: 10px;
  font-style: italic;
  font-weight: bold;
  font-family: monospace;
  color: gray;
  cursor: pointer;
}
.action-text > span:hover {
  text-decoration: underline;
}
#search-area{
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
  width: 1000px;
}
.readme {
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
  font-family: sans-serif;
  line-height: 30px;
}
.grid-count{
  width: 100%;
  float: left;
  font-family: monospace;
  font-weight: bold;
}
.search {
  font-size: 20px;
  margin-top: 10px;
  text-align: center;
  font-family: sans-serif;
}
.search > .action-container > button{
  max-width: 300px;
  min-width: 200px;
  padding: 10px;
  background: white;
  color: gray;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid gray;
  box-shadow: 1px 1px 3px grey;
  font-weight: bold;
  border: 0px;
  margin-right: 10px;
  cursor: pointer;
  font-family: monospace;
}
.search-input-container {
  width: 50%;
  float: left;
  margin-bottom: 20px;
}
.search-input-container:last-child {
  text-align: center;
}
.search-input-container  input {
  width: 350px;
  padding: 10px 7px;
  border: 1px solid black;
  border-radius: 5px;
  font-family: monospace;
  font-size: 14px;
}
.search > .action-container {
  width: 100%;
  float: left;
}
.search > .action-container > button:hover {
  outline: 1px solid gray;
}
.search-input{
  position: relative;
  display: inline-block;
}
.drop-down-container{
  position: absolute;
  max-height: 200px;
  overflow-y: auto;
  width: 100%;
  box-shadow: 1px 1px 3px gray;
  z-index: 1;
  background-color: white;
}
.drop-down-container > div {
  padding: 10px;
  text-align: left;
  font-family: sans-serif;
  font-size: 15px;
  cursor: pointer;
}
.drop-down-container > .selected {
  background-color: #d5d5d5;
}
.export {
  width: 100%;
  float: left;
  text-align: right;
  font-family: monospace;
  font-weight: bold;
  font-size: 15px;  
}
.export > span {
  cursor: pointer;
}
.export > span:hover {
  text-decoration: underline;
}